export const m = 'm'
export const m2 = 'm2'
export const m3 = 'm3'
export const kg = 'kg'
export const each = 'each'

export const GetList = () => {
  return [m, m2, m3, kg, each]
}

export const GetName = (v) => {
  var name

  switch (v) {
    case each:
      name = 'S lng (ci)'
      break;
    case m:
      name = 'Chiu di (m)'
      break;
    case m2:
      name = 'Din tch (m2)'
      break;
    case m3:
      name = 'Th tch (m3)'
      break;
    case kg:
      name = 'Khi lng (kg)'
      break;
  }

  return name;
}