import * as tls_dataUtil from '../TypicalLevelSettingDataUtil'
import * as ls_dataUtil from '../../level/LevelSettingDataUtil'
import { TLS_Data_Wrapper } from './TLS_Data_Wrapper'
import * as genUtil from '../../../../../../util/generalUtil'

// Instance
export const GetFromData = (data, dict) => {
  var qI = new TLS_Data_Wrapper();

  var binding
  // Setting mi: data phn ra thnh binding v typical
  if (data.typical){
    binding = qI.Binding = ls_dataUtil.GetFromDb(data.binding, qI)
    qI.Typical = tls_dataUtil.get(data.typical, qI);
  }
  // Setting c: data chnh l typicallevelsetting
  else {
    binding = qI.Binding = ls_dataUtil.GetDefault(qI);
    qI.Typical = tls_dataUtil.get(data, qI);
  }
  qI.Dict = dict;

  // Gn s kin update Level cho Binding
  genUtil.Add_2(
    binding.OnSet_LevelStorageList_Func_Dict,
    () => qI.LevelStorageList = GetLevelStorageList(qI),
    false
  );

  return qI;
}

export const GetDefault = (dict) => {
  var qI = new TLS_Data_Wrapper();

  var binding = qI.Binding = ls_dataUtil.GetDefault(qI);
  qI.Typical = tls_dataUtil.GetTest(qI);
  qI.Dict = dict;

  // Gn s kin update Level cho Binding
  genUtil.Add_2(
    binding.OnSet_LevelStorageList_Func_Dict,
    () => qI.LevelStorageList = GetLevelStorageList(qI),
    false
  );


  return qI;
}

export const GetTest = (dict) => {
  var qI = new TLS_Data_Wrapper();

  var binding = qI.Binding = ls_dataUtil.GetDefault(qI);
  qI.Typical = tls_dataUtil.GetTest(qI);
  qI.Dict = dict;

  // Gn s kin update Level cho Binding
  genUtil.Add_2(
    binding.OnSet_LevelStorageList_Func_Dict,
    () => qI.LevelStorageList = GetLevelStorageList(qI),
    false
  );


  return qI;
}

// Property
export const GetLevelStorageList = (q) => {
  return q.Binding.LevelStorageList
}